table {
    width: 100%;
    margin: 0 auto;
    text-align: center;
}

tbody tr:nth-child(even) {
    background-color: #ddd;
}

caption {
    font-size: 1.5em;
    margin-block-start: 0.83em;
    margin-block-end: 0.83em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    font-weight: bold;
}
